.flex-box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  width: 100%;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  margin: 10px 0;
  padding: 10px 0;
}

.submit-button:disabled {
  background: #ccc;
  cursor: not-allowed;
}

.submit-button:not(:disabled) {
  -webkit-animation: enable .3s linear;
          animation: enable .3s linear;
}

@-webkit-keyframes enable {
  0% {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
  60% {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
  }
  100% {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
}

@keyframes enable {
  0% {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
  60% {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
  }
  100% {
      -webkit-transform: scale(1);
              transform: scale(1);
  }
}


